 import React, { useState, useCallback } from "react";
import { Grid, Typography, makeStyles } from "@material-ui/core";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import posts from './cardData'
import Chip from '@material-ui/core/Chip';
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";

 const useStyles = makeStyles(theme => ({
  root: {
    paddingLeft: '90px',
    paddingTop: '10px',

  },
  card: {
    maxWidth: 345,
  },
  gridFour: {
    maxWidth: 345,
    margin: '10px'
  },
  media: {
    objectFit: "cover",
  },
  par:{
    margin: '10px',
    textAlign: 'justify'
  },
  expand: {
    maxWidth: 700,
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));


export default function Projects(props) {

  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(-1);
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };


  const handleExpandClick = (i) => {
    setExpanded(expanded === i ? -1 : i );
  };

  return (
    <div className={classes.root}>
      <h1> Projects</h1>
      <Grid container spacing={40} justify="center">
        {posts.map(({title,cardNumber,excerpt,image,images,technologies,source,expandtext,demo},i) => (
          <Grid item key={cardNumber} className={classes.gridFour}>
            <Card>
              <CardActionArea>
                <CardMedia
                  component="img"
                  className={classes.media}
                  height="140"
                  image={image}
                  title="Contemplative Reptile"
                />
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h2">
                    {title}
                  </Typography>
                  <Typography component="p">{excerpt}</Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <Button size="small" color="primary" href={source}>
                  Source
                </Button>
                <Button size="small" color="primary">
                  Demo
                </Button>
                <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded,
            })}

            onClick={() => handleExpandClick(i)}
            aria-expanded={expanded === i}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </IconButton>
        </CardActions>
        <Collapse in={expanded === i} timeout="auto" unmountOnExit>

        <Typography paragraph className={classes.par}>
          {expandtext}
        </Typography>

          <CardContent>
            <Gallery photos={images} onClick={openLightbox}/>
            <ModalGateway>
              {viewerIsOpen ? (
                <Modal onClose={closeLightbox}>
                  <Carousel
                    currentIndex={currentImage}
                    views={images.map(x => ({
                      ...x,
                      srcset: x.srcSet,
                      caption: x.title
                    }))}/>
                 </Modal>
                 ) : null}
              </ModalGateway>

          <h5>Technologies used:</h5>
          {technologies.map((tech,j) => {
            return(
            <Chip
            size="small"
            label={tech}
          /> )
          })}

          </CardContent>
      </Collapse>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  )
}

